@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  scroll-behavior: smooth;
}
.App {
  min-height: 100vh;
  background-color: #eed6c4;
  color: #eed6c4;
  padding-top: 6vw;
  padding-bottom: 70px;
}
@media screen and (max-width: 1100px) {
  .App {
    padding-top: 9vw;
  }
}

@media screen and (max-width: 500px) {
  .App {
    padding-top: 80px;
  }
}
.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Ubuntu", sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: #6b4f4f;
}
@media screen and (max-width: 1000px) {
  .pageTitle {
    font-size: 6.4vw;
  }
}

.header {
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  background-color: #483434;
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-size: 3vw;
  z-index: 100;
}
.header img {
  width: 3vw;
  padding-right: 20px;
}
@media screen and (max-width: 1100px) {
  .header {
    font-size: 5vw;
  }
  .header img {
    width: 5vw;
  }
}
@media screen and (max-width: 500px) {
  .header {
    font-size: 7vw;
    padding: 10px 0px;
  }
  .header img {
    width: 7vw;
  }
}

.media {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #483434;
  position: relative;
  border-radius: 8px;
  font-family: "Poppins", sans-serif;
}
.media:hover {
  background-color: #6b4f4f;
}

.poster {
  border-radius: 8px;
  max-height: 400px;
}
.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}
.sub-title-Container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.sub-title {
  padding: 2px;
}
.bottom-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media screen and (max-width: 550px) {
  .media {
    width: 46%;
  }
  .sub-title {
    font-size: 15px;
  }
}

.ContentModal_landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal_portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal_about {
  padding: 20px 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Ubuntu", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal_title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.ContentModal_description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
  margin: 10px 0px;
}

.ContentModal_description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal_landscape {
    display: none;
  }
  .ContentModal_portrait {
    display: flex;
    width: 28%;
  }
  .ContentModal {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 0;
  }
}

.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Series {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

